'use client';
import { useState, useEffect } from 'react';
import Link from 'next/link';
import { useDispatch, useSelector } from 'react-redux';
import { useRouter } from 'next/navigation';
import { setSession } from '@/app/_lib/sessionReducer';
import { useSession, signOut } from 'next-auth/react';
import { FaUserCircle } from 'react-icons/fa';
import Cookies from 'js-cookie';
import { fetchData } from '@/app/_lib/userReducer';

export default function UserMenu() {
  const [isOpen, setIsOpen] = useState(false);
  const dispatch = useDispatch();
  const router = useRouter();
  const { data: session } = useSession();
  const userData = useSelector((state) => state.userData?.user);

  useEffect(() => {
    const userIdFromCookie = JSON.parse(Cookies.get("shopflow_session") || null);
    const userId = userIdFromCookie?.user?.id;
    const token = userIdFromCookie?.token;

    if (userId) {
      dispatch(setSession(userIdFromCookie));
      dispatch(fetchData(`user/userProfileInfo?userId=${userId}`, token));
    }
  }, [dispatch]);
   useEffect(()=>{},[userData]);
  const toggleDropdown = () => setIsOpen((prev) => !prev);

  const handleSignOut = async () => {
    try {
      await signOut({ callbackUrl: '/' });
      Cookies.remove("shopflow_session");
      dispatch(setSession(null));
      router.refresh();
    } catch (error) {
      console.error('Sign-out error:', error);
    }
  };

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (!document.getElementById('dropdownAvatar')?.contains(event.target)) {
        setIsOpen(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  return (
    <div className="relative">
      <button
        id="dropdownUser AvatarButton"
        onClick={toggleDropdown}
        className="flex items-center text-sm bg-gray-800 px-3 py-1 rounded-md focus:outline-none"
        type="button"
        aria-expanded={isOpen}
        aria-haspopup="true"
      >
        <div className="flex items-center">
          <div className="w-8 h-8 rounded-full overflow-hidden bg-gray-500 flex justify-center items-center mr-2">
            {userData?.profile_pic ? (
              <img
                src={userData.profile_pic || '/_assets/user.png'}
                alt="User  Profile"
                className="w-full h-full object-cover"
              />
            ) : (
              <FaUserCircle className="text-white text-3xl" />
            )}
          </div>
          <span className="font-medium text-white hover:text-orange-500">
            {userData?.name || 'Sign in'}
          </span>
        </div>
      </button>

      {isOpen && (
        <div
          id="dropdownAvatar"
          className="absolute right-0 z-10 bg-white rounded-lg shadow w-56 dark:bg-gray-700"
          aria-hidden={!isOpen}
        >
          {userData?.name ? (
            <ul className="text-sm text-gray-700 dark:text-gray-200">
              <li className="px-4 py-3">
                <button
                  className="w-full text-left block bg-orange-500 text-white font-medium py-2 px-3 rounded-lg hover:bg-orange-600"
                  onClick={handleSignOut}
                >
                  Sign out
                </button>
              </li>
              <li className="px-4 py-2">
                <Link href="/user/profile2" className="block hover:text-orange-500 hover:underline">
                  My Account
                </Link>
              </li>
             
            </ul>
          ) : (
            <div className="p-4 text-center">
              <Link href="/auth" className="block mb-2 text-orange-500 font-medium text-sm">
                Sign in or New Customer start here..
              </Link>
            </div>
          )}
        </div>
      )}
    </div>
  );
}